@import "./styles/global-imports";
@import "./styles/utils/reset";

.app {
  @extend %body-text;
  background-color: $c-light-pink;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;

  &__main {
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
  }
}
